import React from 'react';
import PropTypes from 'prop-types';
// import { Link } from 'react-router-dom';

export default function Navbar(props) {
    const pickColor = (event) => {
        props.changeColor(event.target.value)
    }
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
            <div className="container-fluid">
                <a className="navbar-brand" href="#">{props.navbarTitle}</a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        {/* 
                        <li className="nav-item">
                            <Link className="nav-link active" aria-current="page" to="/">{props.homeTitle}</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/about">{props.aboutTitle}</Link>
                        </li> */}
                    </ul>
                    {/* <form className="d-flex">
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button className="btn btn-outline-success" type="submit">Search</button>
                    </form> */}
                    <label id="exampleColorInputLabel" htmlFor="exampleColorInput" className="form-label">Choose Dark Color</label>
                    <input type="color" className="form-control form-control-color mx-2" id="exampleColorInput" onChange={pickColor} value={props.color} title="Choose your color" />
                    <div className="form-check form-switch my-2">
                        <input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckChecked" onClick={props.modeChanger} />
                        <label className={`form-check-label text-${props.mode === "light" ? "dark" : "light"}`} htmlFor="flexSwitchCheckChecked">{props.modeText}</label>
                    </div>
                </div>
            </div>
        </nav >
    )
}

Navbar.propTypes = {
    navbarTitle: PropTypes.string.isRequired,
    homeTitle: PropTypes.string.isRequired,
    aboutTitle: PropTypes.string.isRequired
}

Navbar.defaultProps = {
    navbarTitle: "TextUtils",
    homeTitle: "Home",
    aboutTitle: "About Us"
}