import { useState } from "react";
import Navbar from "./components/Navbar";
import TextBox from "./components/TextBox";
import Alert from "./components/Alert";
// import About from "./components/About";
// import {
//   BrowserRouter as Router,
//   Switch,
//   Route
// } from "react-router-dom";

function App() {
  const [mode, updateMode] = useState("light");
  const [modeText, modeTextUpdate] = useState("Light Mode Enabled");
  const [alert, changeAlert] = useState(null);
  const [color, changeColor] = useState("black");

  const showAlert = (message, type) => {
    changeAlert({
      message: message,
      type: type
    });
    setTimeout(() => {
      changeAlert(null);
    }, 1500);
  }

  const modeChanger = () => {
    if (mode === "light") {
      updateMode("dark");
      document.body.style.backgroundColor = color;
      changeColor("black");
      document.body.style.color = "white";
      document.getElementById("exampleColorInputLabel").style.display = "none";
      document.getElementById("exampleColorInput").style.display = "none";
      modeTextUpdate("Dark Mode Enabled");
      showAlert("Dark Mode Has Been Enabled", "success");
    }
    else {
      updateMode("light");
      document.body.style.backgroundColor = "white";
      document.body.style.color = "black";
      document.getElementById("exampleColorInputLabel").style.display = "block";
      document.getElementById("exampleColorInput").style.display = "block";
      modeTextUpdate("Light Mode Enabled");
      showAlert("Light Mode Has Been Enabled", "success");
    }
  }
  return (
    <>

      {/*
        importance of using exact keyword for matching exact path
        /user --> component 1
        /user/home --> component 2 
      */}
      {/* <Router> */}
      <Navbar navbarTitle="Text-Utils" homeTitle="Home" aboutTitle="About" mode={mode} modeChanger={modeChanger} modeText={modeText} changeColor={changeColor} color={color} />
      <Alert alert={alert} />
      {/* <Switch>
          <Route exact path="/about">
            <About />
          </Route> */}
      {/* <Route exact path="/"> */}
      <TextBox changeAlert={changeAlert} showAlert={showAlert} label="Enter The Text Below To Analyze" />
      {/* </Route>
        </Switch>
      </Router> */}
    </>
  );
}

export default App;