import React, { useState } from 'react';
import PropTypes from 'prop-types';

export default function TextBox(props) {
    const [text, updateText] = useState("");

    const finalText = (event) => {
        updateText(event.target.value);
    }
    const convertToUpper = () => {
        updateText(text.toUpperCase());
        props.showAlert("Converted To Uppercase", "success");
    }
    const convertToLower = () => {
        updateText(text.toLowerCase());
        props.showAlert("Converted To LowerCase", "success");
    }
    const clear = () => {
        updateText("");
        props.showAlert("Textbox Cleared", "success");
    }
    const copyText = () => {
        document.getElementById("textBox").select();
        document.execCommand("copy");
        props.showAlert("Copied To Clipboard", "success");
    }
    return (
        <div className="container my-4">
            <div className="row">
                <div className="mb-3">
                    <label htmlFor="textBox" className="form-label"><p className="h4">{props.label}</p></label>
                    <textarea className="form-control mb-2" placeholder="Enter Something To Convert!!" value={text} onChange={finalText} id="textBox" rows="5"></textarea>
                    <input type="button" value="Convert to Uppercase" onClick={convertToUpper} className="btn btn-primary mx-1 my-1" />
                    <input type="button" value="Convert to Lowercase" onClick={convertToLower} className="btn btn-primary mx-1 my-1" />
                    <input type="button" id="copy" value="Copy" onClick={copyText} className="btn btn-secondary mx-1 my-1" />
                    <input type="button" value="Clear" onClick={clear} className="btn btn-danger mx-1 my-1" />
                </div>
                <div className="container">
                    <div className="row">
                        <h2>Your Text Summary</h2>
                        <p>{text.split(" ").length - 1} Words and {text.length} Characters</p>
                        <p>{0.008 * (text.split(" ").length - 1)} Minutes to Read</p>
                        <h2>Preview</h2>
                        <p>{text.length === 0 ? "Enter Something In The Text Box Above To Preview It Here!!" : text.toLowerCase()}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

TextBox.propTypes = {
    label: PropTypes.string.isRequired
}

TextBox.defaultProps = {
    label: "Enter the text below"
}